// flex helpers
.flex {
    display: flex;
    >*.full {
        flex: 1;
    }
    &.column {
        flex-direction: column;
    }
    &.justify-center {
        justify-content: center;
    }
    &.justify-space-between {
        justify-content: space-between;
    }
    &.align-center {
        align-items: center;
    }
    &.wrap {
        flex-wrap: wrap;
    }
    &.column-reverse {
        flex-direction: column-reverse;
    }
    &.justify-flex-end {
        justify-content: flex-end;
    }
    &.align-flex-start {
        align-items: flex-start;
    }
    &.full {
        flex-grow: 1;
    }
}

.grid{
    display: grid;
}

.clean-list {
    list-style-type: none;
    margin: 0;
    padding: 0;
}

.clean-link {
    text-decoration: none;
    color: inherit;
}

.hide {
    display: none;
}

// btns
.primary-btn { 
    width: 100%;
    padding: 12px 12px;
    background-color: $main-purple;
    color: $medium-gray;
    border: none;
    border-radius: 8px;
}

.pointer{
    cursor: pointer;
}

