.user-modal {

    background-color: $medium-dark;
    color: $medium-gray;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    padding: 20px 10px 10px;
    border-top-left-radius: 20px;
    border-top-right-radius: 20px;
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    height: 90vh;
    transition: transform .300s;
    box-shadow: 0 -.3px 0 0 $medium-gray;


    .links-container {
        flex-grow: 1;
    }

    h5 {
        margin: 0;
    }

    .modal-item {
        display: flex;
        align-items: center;
        padding: 0 10px;
        width: 100%;
        height: 33px;
        font-size: 0.95em;
        font-weight: 500;
        border-radius: 10px;
        letter-spacing: .2px;

        &:hover {
            opacity: 0.8;
        }

        @include for-normal-layout {
            border-radius: 5px;
        }
    }

    .logout {
        margin-top: 10px;
        font-weight: 100;
        position: relative;

        &::before {
            content: "";
            box-shadow: 0 -.25px 0 0 $medium-gray;
            width: 100%;
            height: 10px;
            position: absolute;
            left: 0;
            top: -10px;
        }
    }

    @include for-normal-layout {
        width: 233px;
        height: 225px;
        bottom: unset;
        left: unset;
        right: unset;
        border-radius: 8px;
        padding: 10px;
        transform: translate(calc(-100% + 30px), 5px);
        box-shadow: .3px 0 0 0 $medium-gray, 0 .3px 0 0 $medium-gray, -.3px 0 0 0 $medium-gray, 0 -.3px 0 0 $medium-gray;

        .logout {
            &::before {
                box-shadow: -.2px -.25px 0 0 $medium-gray;
            }
        }
    }
}